$primary: hsl(246, 80%, 60%);
$work: hsl(15, 100%, 70%);
$play: hsl(195, 74%, 62%);
$study: hsl(348, 100%, 68%);
$exercise: hsl(145, 58%, 55%);
$social: hsl(264, 64%, 52%);
$selfcare: hsl(43, 84%, 65%);
$Very-dark-blue: hsl(226, 43%, 10%);
$Dark-blue: hsl(235, 46%, 20%);
$Desaturated-blue: hsl(235, 45%, 61%);
$Pale-Blue: hsl(236, 100%, 87%);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  font-weight: 300;
  color: hsl(0, 0, 100%);
}

.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-color: $Very-dark-blue;
  padding: 10vh 0;
}

.active {
  color: white;
}

.left-col {
  //   display: inline-flex;
  //   flex-direction: column;
  flex-wrap: wrap;
  //   overflow: hidden;
  //   min-height: 70vh;
  width: 20%;
  border-radius: 0.5rem;
  background-color: $Dark-blue;
}

.user-info {
  min-height: 45vh;
  background-color: $primary;
  border-radius: 0.5rem;
  padding: 1em;
  // display: none;
}

.user-info p {
  margin-top: 1rem;
  font-size: 1rem;
  color: rgb(240, 240, 240);
}

.user-info h3 {
  margin-bottom: 1rem;
  font-size: 3vw;
}

.user-info img {
  max-width: 40%;
  height: auto;
  border: 2px solid hsl(0, 0, 100%);
  border-radius: 50%;
}

.duration-filter {
  // padding: 0 1rem;
  color: $Desaturated-blue;
  min-height: 20vh;
}

li {
  list-style-type: none;
  padding: 1rem;
  color: $Desaturated-blue;
  font-size: 1.8vw;
}

li:hover {
  color: white;
  cursor: pointer;
}
.right-col {
  width: 60%;
  height: 70vh;
  display: flex;
}
.card-container {
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  // gap: 1rem;
  justify-content: space-between;
  // vertical-align: auto;
  margin-left: 1rem;
}

.card {
  width: 18vw;
  height: 35vh;
}
.activity-icon {
  height: 10vh;
  border-radius: 0.5rem;
  display: block;
}

.Work {
  background: url("../img/icon-work.svg") no-repeat;
  background-position: right;
  background-color: $work;
}

.Play {
  background: url("../img/icon-play.svg") no-repeat;
  background-position: right;
  background-color: $play;
}
.Study {
  background: url("../img/icon-study.svg") no-repeat;
  background-position: right;
  background-color: $study;
}
.Exercise {
  background: url("../img/icon-exercise.svg") no-repeat;
  background-position: right;
  background-color: $exercise;
}

.Social {
  background: url("../img/icon-social.svg") no-repeat;
  background-position: right;
  background-color: $social;
}

.SelfCare {
  background: url("../img/icon-self-care.svg") no-repeat;
  background-position: right;
  background-color: $selfcare;
}

.activity-card {
  background-color: $Dark-blue;
  border-radius: 0.5rem;
  position: relative;
  top: -5vh;
  padding: 1rem;
  // min-height: 35vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.activity-card:hover {
  background-color: $Desaturated-blue;
}
.card-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.activity-title {
  font-size: 1rem;
  font-weight: 500;
  color: white;
}

.icon-ellipsis {
  background: transparent;
  border: none;
}

.icon-ellipsis:hover {
  cursor: pointer;
}

.current {
  font-size: 32px;
  margin: 1rem 0;
}

.previous {
  font-size: 12px;
  color: $Pale-Blue;
}

